@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville|Open+Sans);
* {
  font-family: 'Open Sans', 'Roboto', sans-serif;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
a {
  font-family: 'Libre Baskerville', 'Merriweather', serif;
}

.title h1 {
  font-size: 1.7em;
}

.app {
  text-align: center;
}

a {
  color: black;
}

.title a {
  color: white;
}

.appLogo {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
  height: 70px;
  margin-top: 20px;
}

.scriptLogo {
  height: 70px;
  margin-top: 20px;
}

.appHeader {
  background-color: rgb(54, 46, 46);
  padding: 20px;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

@-webkit-keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Scrollbar */

body::-webkit-scrollbar {
  width: 10px;
  background-color: #F5F5F5;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #F5F5F5;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: #555;
}
.modal{
    height: 50vhpx;
    width: 70vw;
    display:flex;
    align-items: center;
    justify-content: center;
    /* background-color:black; */
}
.modal-content,.modal-footer{
    background-color:rgb(224, 189, 189);

}
.cardsContainer {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	width: 95%;
	margin: 0 auto;
}

.cardsContainer {
	flex: 1 1;
}

.card:nth-child(odd) {
	border-right: 1px solid grey;
}

.card:nth-child(even) {
	border-right: 1px solid white;
	border-left: 1px solid grey;
	margin-left: -1px;
}

.author {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-between;
}

.content {
	padding-top: 0;
	width: 58%;
	padding: 5px;
}

.card {
	display: flex;
	flex-direction: row;
	width: 49%;
	padding: 5px;
	margin: 10px 0;
	border-bottom: 1px solid grey;
	background-color:rgb(238, 238, 238)

}

.image {
	padding-left: 1rem;
	width: 40%;
	margin-bottom: 0;
}

.cardsContainer img {
	max-width: 300px;
	width: 100%;
	
}

@media screen and (max-width: 1200px) {
	.cardsContainer {
		display: flex;
		flex-direction: column;
	}
	.card:nth-child(odd) {
		border-right: 0;
	}
	.card {
		width: 90%;
	}
}

@media screen and (max-width: 600px) {
	.card .image {
		order: -2;
		width: 80%;
		padding-left: 0;
		margin-top: 2rem;
	}
	.card:nth-child(even) {
	margin-left: inherit;
	}
	.card {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 5px;
		width: 80%;
		margin: 0 auto;
	}
	.content {
		width: 80%;
	}
	.cardsContainer {
		justify-content: center;
		align-items: center;
	}
}
.outletSection {
    width: 60%;
    margin: 0 auto;
    margin-top: 1rem;
    padding: 10px;
    border-style: none none double none;
    border-color:#757575;
    border-width:5px;
}

.generalInfo {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.generalInfo>* {
    margin: 0 8px;
}

.flagCode {
    width: 32px;
    height: 20px;
}
select {
  font-size: 1.1em;
  width: 20%;
  margin-top: 5px;
  padding-left: 5px;
  outline: none;
  border: 0;
  border-bottom: 1px solid #757575;
  margin-bottom: 2px;
  outline: none;
}
@media screen and (max-width: 1200px) {
	select{
    width:35%;
  }
}
@media screen and (max-width: 500px) {
	select{
    width:80%;
  }
}



html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  line-height: 1.4;
}

a {
  border-bottom: 2px solid #ffc600;
  text-decoration: none;
}

a:hover {
  border-bottom: 2px solid #FF8F00;
}

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}
